@import "https://fonts.googleapis.com/css?family=Lato:100,300,400,700,900";


/* Body Style */

.mainbody{
  margin: auto;
  padding: 0;
  border: 0;
  background-color:#007399;
  color: #fff;
  font-family: "Lato", sans-serif;
  display: block;
  position: relative;
}


/* Header style */

header{
  margin: auto;
  background: -webkit-linear-gradient(top ,black 1%, transparent 100%);
  background: linear-gradient(to bottom, black 5%, transparent 100% );
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  padding: 20px 40px;
  position: fixed;
  width: 100vw;
  box-sizing: border-box;
  z-index: 5; 
}

.logoimg{
  width: 201px;
  height: 121px;
  transition: margin .125s ease;
  -webkit-transition: margin .125s ease;
}

.logoimg:hover{
  cursor: pointer;
}

.menubar{
  margin: 0;
  position: absolute;
  padding: 40px 20px 0px;
  display: block;
  filter: opacity(0.5) drop-shadow(0 0 0 white);
}

.menubar .menu-icon{
  width: 28px;
  margin-left: 20px;
}

.navbar-default{
  display: flex;
  flex-direction: row;
}

.navbar-default ul{
  margin: 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  height: 37px;
  width: 100%;
  -webkit-box-align: center;
  -webkit-align-item:center;
  align-items: center;
  padding: 35px 10px 0px;
}

ol, ul{
  list-style: none;
}

.link-menu{
  text-decoration: none;
  color: #ddd;
  font-weight: 300;
  padding: 8px 15px;
  font-size: 14px;
  border-radius: 13px;
}

.link-menu:hover{
  background: #e50914;
  cursor: pointer;
}

.search{
  margin: 0;
  width: 100%;
  position: absolute;
  left: 0;
  padding: 35px 190px 0px;
  display: flex;
  pointer-events: none;
  align-items: center;
  justify-content: center;
  
}

.search input{
  font-family: "Lato", sans-serif;
  font-weight: 300;
  font-size: 14px;
  height: 37px;
  width: 20%;
  background: transparent;
  color: #fff;
  border: 0;
  border: 2px solid rgba(245, 245, 241, 0.1);
  outline: none;
  border-radius: 37px;
  padding: 0 10px;
  pointer-events: all;
  transition: border .5s ease, width .125s ease .125s;
}

.search input:hover{
  border:  2px solid #e50914;
  width: 30%;
}

/* Profile user */
.profile{
  margin-left: auto;
  margin-right: 40px;
}

.profile .user{
  display: flex;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  margin: 30px 20px 0 0;
}

.profile .user .userimage{
  border-radius: 44px;
  margin-top: 5px;
  margin-left: 10px;
  overflow: hidden;
  width: 44px;
  height: 44px;
  box-sizing: border-box;
  border: 3px solid transparent;
  transition: border .125s ease;
  -webkit-transition: border .125s ease;
}

.profile .user .userimage img{
  width: 100%;
  display: block;
}

.profile .user .username{
  display: flex;
  font-size: 15px;
  font-weight: 300;
  height: 44px;
  color: #fff;
  align-items: center;
  -webkit-box-align: center;
  -webkit-align-items:center
}

.profile .user:hover{
  cursor: pointer;
}

.profile .user:hover .userimage{
  border: 3px solid #e50914;
}

.dropbtn {
  margin-top: 8px;
  margin-left: 20px;
  padding: 6px 6px;
  width: 30px;
  border: none;
  cursor: pointer;
  display: none;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #2bcc9c;
  min-width: 100px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
  margin-top: 20px;
  margin-right: 20px;
  border-radius: 10px;
}

.dropdown-content a {
  color: black;
  padding: 6px 10px;
  text-decoration: none;
  display: block;
}

.dropdown-content a:hover {
  border-radius: 10px;
  background-color: #f1f1f1
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown:hover .dropbtn {
  background-color: transparent;
}


/* Main Content style */

.maincontent{
  margin: auto;
  padding: 40px 100px;
  display: flex;
  flex-direction: row;
  position: relative;
  margin-bottom: -15vw;
  -webkit-box-align: center;
  -webkit-align-item:center;
  align-items: center;
  justify-content: space-around;  
}

.content{
  margin: 0;
  width: 30%;
  padding: 14vw 7vw;
}

.movieposter .poster{
  align-items: center;
  max-width: 100%;
  filter: opacity(0.5) drop-shadow(0 0 0 rgb(218, 22, 8));
}

.maincontent .content p{
  text-align: justify;
  font-size: 14px;
  font-weight: 200;
}

.maincontent .content .buttons-act{
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  width: 400px;
  margin-top: 30px;
}

.maincontent .content .buttons-act .btn-main:first-child{
  margin-right: 10px;
}

.btn-main{
  background: transparent;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  position: relative;
  -webkit-box-align: center;
  align-items: center;
  text-align: center;
  text-decoration: none;
  color: #fff;
  padding: 20px;
  box-sizing: border-box;
  border: 2px solid rgba(245, 245, 241, 0.2);
  border-radius: 44px;
  font-size: 14px;
  font-weight: 600;
}

.btn-main[data-primary='true']{
  border: 2px solid #e50914;  
  background-color: #e50914;
  
}

.btn-main{
  border: 2px solid #f5f5f1;
  cursor: pointer;
}

.maincontent .overlay{
  height: 100%;
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
}


/* Movie Advertisment images */

.movie-ad{
  margin: 0;
  padding: 200px 180px 100px ;
}

.each-slide > div {
  display: flex;
  align-items: center;
  justify-content: center;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 90% 90%;
  height: 450px;
  width: 100%;
}


/* Title list movies style */

.TitleList{
  margin: auto;
  display: block;
  width: 100%;
}
.title{
  margin: 0;
  padding: 30px 0;
  text-align: center;
}
.title h1{
  color: #fff;
  font-size: 30px;
  padding-left: 30px;
}

.trandmovies {
  border: 1px solid #ddd; 
  border-radius: 7px;
  opacity: 1;
  transition: .5s ease;
  backface-visibility: hidden;
  width: 160px;
  height: 200px;
  margin: 50px 10px 0px;
  padding: 10px;
  cursor: pointer;
  justify-content: space-around;
}

.trandmovies:hover{
  box-shadow: 0 0 2px 1px rgba(7, 10, 6, 0.5);
  transform: scale(1.5);
  -webkit-transform: scale(1.5);
  -ms-transform: scale(1.5);
  opacity: 0.3;
}
.imgContainer {
  margin: auto;
  justify-content: center;
  cursor: pointer;
  display: inline-block;
  position: relative;
}

.imgContainer:hover .text {
  opacity: 1;
}

.text {
  color: white;
  font-size: 11px;
  padding: 10px 32px;
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  top: 30%;
  left: 40%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%)
}
.text::after{
  content: icon;
}

/* Footer style */
footer{
  margin: auto;
  padding: 10px 0 0;
  position: relative;
  display: block;
  background: -webkit-linear-gradient(bottom ,black 0.1%, transparent 100%);
  background: linear-gradient(to top, black 0.5%, transparent 100% );
}

.footer h3{
  padding: 40px 0 0;
  font-size: 20px;
  color: #b9d5dd;
  text-align: center;
}
.about-lists{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 100%;
  align-items: center;
}
.listtext{
  margin: auto;
  justify-content: center;
  cursor: pointer;
  display: flex;
  position: relative;
  text-align: left;
}
.lists ul li{
  margin: auto;
  font-size: 13px;
  color: #b9d5dd;
  font-weight: 200;
}
.link-a{
  text-decoration: none;
  color: saddlebrown;
  font-family: 'Comfortaa', cursive;
}
.link-a:hover{
  cursor: pointer;
  color: #b9d5dd;
}
.bottom-text{
  margin: auto;
  text-align: center;
  position: relative;
  display: block;
  font-size: 10px;
  margin: 0 20px;
  color: #b9d5dd;
  padding: 5px 0 20px;
}
.bottomtext-line{
  width: 10%;
  margin: 40px auto 0;
  text-align: center;
}
.bottom-text span{
  color: darkorange;
}

.footer-image{
  margin: auto;
  justify-content: center;
  display: flex;
  position: relative;
  padding-top: 30px;
}
.image-foot{
  width: 10%;
  padding-left: 10px;
  filter: grayscale(80%);
}

.image-foot:hover{
  cursor: pointer;
  filter: grayscale(0%);
}

/* Media query for all process */

@media only screen and (max-width:1440px){
  .mainbody{
    background: #007399;
    overflow: hidden;
    width: 100%;
  }

  .maincontent{
    padding: 50px 40px;
  }
  
}

@media only screen and (max-width:1250px){
  .search {
    width: 70%;
    padding: 35px 370px 0px;
  }

  .maincontent{
    padding: 50px 40px;
  }
  
  .movie-ad{
    padding: 100px 50px 100px ;
  }

  .each-slide > div {
    background-size: 80% 80%;
    height: 450px;
    width: 100%;
  }
}

@media only screen and (max-width:1080px){

  .username{
    display: none;
    visibility: hidden;
  }
  .userimage:hover{
    content: counter(hi);
  }
  .about-lists{
    display: inline-block;
  }
 
  .maincontent{
    padding: 50px 40px;
  }

  .movie-ad{
    padding: 100px 50px 100px ;
  }

  .each-slide > div {
    background-size: 80% 80%;
    height: 450px;
    width: 100%;
  }
  
  .image-foot{
    width: 5%;
  }
}

@media only screen and (max-width:950px){
